<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar toolbar2">
            <el-form :inline="true" size="medium">
                <el-form-item style="margin: auto;">
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table size="medium" :data="tableData" v-loading="listLoading"
                  style="width: 100%;">
            <el-table-column type="index" width="40"></el-table-column>
            <el-table-column prop="zh" label=""></el-table-column>
            <el-table-column prop="js" label="" :formatter="formatterJS"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope" v-if="scope.row.zh !== 'admin'">
                    <!--<el-button size="mini" @click=""></el-button>-->
                    <el-button size="mini" @click="resetMM(scope.row)"></el-button>
                    <!--<el-button size="mini" type="danger" @click="deleteUser(scope.row)"></el-button>-->
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false" width="40%">
            <el-form :model="addForm" label-width="120px" ref="addForm">
                <el-form-item
                        label=""
                        prop="zh"
                        :rules="[{ required: true, message: '', trigger: 'blur' }]">
                    <el-input v-model="addForm.zh"></el-input>
                </el-form-item>
                <el-form-item label=""
                              prop="mm"
                              :rules="[{ required: true, message: '', trigger: 'blur' }]">
                    <el-input v-model="addForm.mm"></el-input>
                </el-form-item>
                <el-form-item label="" prop="js"
                              :rules="[{ required: true, message: '', trigger: 'blur' }]">
                    <el-select v-model="addForm.js" style="width:100%;">
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>

        <!--<el-dialog title="" :visible.sync="resetPassword" :close-on-click-modal="false">-->
            <!--<el-form label-width="120px" ref="addForm">-->
                <!--<el-form-item-->
                        <!--label="">-->
                    <!--<el-input v-model="addForm.zh" readonly></el-input>-->
                <!--</el-form-item>-->
                <!--<el-form-item label="">-->
                    <!--<el-input v-model="newPassword"></el-input>-->
                <!--</el-form-item>-->
            <!--</el-form>-->
            <!--<div slot="footer" class="dialog-footer">-->
                <!--<el-button @click.native="resetPassword = false"></el-button>-->
                <!--<el-button type="primary" @click.native="resetPwdSubmit" :loading="addLoading"></el-button>-->
            <!--</div>-->
        <!--</el-dialog>-->
    </section>
</template>

<script>
    export default {
        data() {
            return {
                //,
                tableData: [],
                listLoading: false,
                addFormVisible: false,//
                resetPassword: false,//
                addLoading: false,
                //
                addForm: {},
            }
        },
        created(){
            this.getData();
        },
        methods: {
            resetMM(row) {
                this.$axios.post('/user/resetPassword', {id: row.id}).then(res => {
                    this.$message.success('666666');
                })
            },
            formatterJS(row){
                return row.js == 0 ? '' : '';
            },
            //
            getData() {
                this.listLoading = true;
                this.$axios.get('/user/findAll').then((res) => {
                    this.listLoading = false;
                    this.tableData = res.data;
                });
            },
            //
            handleAdd: function () {
                this.passwordShow = this.addFormVisible = true;
                this.addForm = {};
            },
            //
            addSubmit: function () {
                console.log(this.addForm)
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$axios.post('/user/add', this.addForm).then((res) => {
                            this.$message.success('!');
                            this.getData();
                            this.addFormVisible = false;
                            this.$refs.addForm.resetFields()
                        });
                    }
                });
            },
            resetPwd(index, row){
                this.addForm = Object.assign({}, row); //
                this.addForm.mm = '';
                this.resetPassword = true;
            },
            resetPwdSubmit(){
                let param = {
                    id: this.addForm.id,
                    newword: this.newPassword,
                    rzdm: this.$rzdm
                };
                let url = this.$url + '/user/changepassword';
                this.$axios.post(url, this.$qs.stringify(param)).then(res => {
                    if (res.data.resCode == 200) {
                        this.getData()
                        this.resetPassword = false;
                        this.$message({
                            message: ',!',
                            type: 'success'
                        });
                    } else {
                        this.$message.error(res.data.resMsg);
                    }
                })
            }
        },
    }
</script>

<style>
    /*.el-input__inner, .el-textarea__inner {*/
    /*color: #707276;*/
    /*font-weight: normal;*/
    /*}*/
    /*.el-textarea__inner {*/
    /*font-size: 16px;*/
    /*}*/
    /*.el-input.is-disabled .el-input__inner, .el-textarea.is-disabled .el-textarea__inner {*/
    /*background-color: #FAFAFA;*/
    /*border-color: #DCDFE6;*/
    /*color: #707276;*/
    /*}*/

    .file-dialog .content {
        width: 100%;
        height: 800px;
        background: #cccccc;
    }

    .el-dialog--small {
        width: 80%;
    }
</style>


